import React, { useState } from "react";
import "./App.css";
import Home from "./Components/Home";
import Preloader from "./Components/preloader";
function App() {
    const [loading, setLoading] = useState(true);

    setTimeout(() => {
        setLoading(false);
    }, 0);

    return <>{loading ? <Preloader open={loading} /> : <Home />}</>;
}

export default App;
