import React from "react";
import Poster from "../../posters/eyeswideshut.jpg";
import Sposter from "../../posters/ldr.jpg";
function index() {
    return (
        <>
            <div className="week-movie-container">
                <div className="title-wrapper">
                    <h1> + This Week Movie +</h1>
                    <h2>(eyes wide shut)</h2>
                </div>
                <div className="week-movie-wrapper">
                    <img loading="lazy" src={Poster} alt="" />
                    <img loading="lazy" src={Sposter} alt="" />
                </div>
            </div>
        </>
    );
}

export default index;
